<template lang="jade">
  div#auth
    h2#title 
    template
      el-table(v-if="users", :data='users', border='', style='width: 95%')
        el-table-column(v-for="(val, key, index) in base", :prop="key", :label="val", :key="key", sortable)
        el-table-column(label='')
          template(scope='scope')
            el-button.btn-black(size='small', type='warning', @click='addToBlackList(scope.$index)') 
            el-button(size='small', type='danger', @click='deleteUser(scope.$index)') 
</template>
<script>
  import api from '../../common/api-admin'
  export default {
    name: 'auth',
    data () {
      return {
        base: {
          name: '',
          number: '',
          school: '',
          sex: '',
          mail: ''
        },
        msg: '',
        users: ''
      }
    },
    beforeCreate() {
      api.users(0).then((res) => {
        this.users = res
      })
    },
    methods: {
      deleteUser(index) {
        let user = this.users[index]
        this.$confirm(':' + user.name, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          api.deleteUser(user.number).then((res) => {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.users.splice(index, 1);
          })
        }).catch(() => {});
      },
      addToBlackList(index) {
        let user = this.users[index]
        this.$prompt(':', ':"' + user.name + '"', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          api.addToBlackList({
            number: user.number,
            message: value
          }).then((res) => {
            this.users.splice(index, 1);
            this.$message({
              type: 'success',
              message: res.msg
            });
          })
        }).catch(() => {});
      }
    }
  }
</script>
<style lang="sass">
  #app
    height: 100%
  #auth
    padding-bottom: 2rem
    flex: 1
    margin-left: 12rem
    padding-left: 1rem
    background-color: #EFF2F7
    #title
      margin: 20px
      font-size: 2rem
    .btn-black
      background-color: #3e5059
      border-color: #607D8B
</style>



