import fileMap from './fileMap'
export default {

  /* n
   *  @param1:arr, 
   *  @param2:n, n
   */
  toNArray(arr, n) {
    let nArr = [], arr_ = []
    arr.forEach((item, index) => {
      arr_.push(item)
      if ((index + 1) % n === 0 || index === arr.length - 1) {
        nArr.push(arr_)
        arr_ = []
      }
    })
    return nArr
  },

  /* 
   *  @param1:url, 
   *  @param2:data, {key:val},
   */
  download(url, data) {
    let form = document.createElement('form')
    form.setAttribute('style', 'display: none')
    form.setAttribute('target', '')
    form.setAttribute('method', 'post')
    form.setAttribute('action', url)
    for (let key in data) {
      let input = document.createElement('input')
      input.setAttribute('name', key)
      input.setAttribute('value', data[key])
      document.body.appendChild(form)
      form.appendChild(input)
    }
    form.submit()
  },

  /* :input(type='file'), 
   *  @param1:fileType, 
   *  @param2:cb  {
   *    name: ,
   *    url: ()
   *    file: ,
   *  }
   */
  upload(fileType, multiple, cb) {
    let input = document.createElement('input')
    input.type = 'file'
    if (multiple) input.multiple = true
    document.body.appendChild(input)
    input.click()
    input.onchange = (evt) => {
      let files = evt.target.files
      for (let i = 0; i < files.length; i++) {
        let file = evt.target.files[i]
        let type = file.type && file.type.split('/')[1].trim()
        if (fileMap[fileType].indexOf(type) != -1) {
          if (!multiple) {  //
            if (fileType == 'img') {  //
              let reader = new FileReader()
              reader.onloadend = (e) => {
                cb({
                  picture: file.name,  // xxx.jpg
                  url: e.target.result,
                  file: file
                })
              }
              reader.readAsDataURL(file)
            } else {
              cb({file: file})
              return
            }
          }
        } else {
          cb({err: true})
          return
        }
      }
      cb({files: files})
    }
    input.style.display = 'none'
  },

  // Object
  generateForm (data) {
    console.log(data)

    window.testData = data
    let formData = new FormData()
    for (var key in data) {
      if(data[key] instanceof FileList) { // 
        for(let i = 1; i <= data[key].length; i++)
        formData.append('img' + i, data[key][i-1])
      } else {
        formData.append(key, data[key])
      }
    }
    return formData
  },

  // url,object
  postData (url, data) {
    return {
      url: url,
      data: this.generateForm(data),
      method: 'POST'
    }
  },

  // : yyyy-mm-dd
  getStandardDate (date) {
    if (!(date instanceof Date)) date = new Date(date)
    var y = date.getFullYear(),
      m = date.getMonth() + 1,
      d = date.getDate()
    if (m < 10) m = '0' + m
    if (d < 10) d = '0' + d
    return y + '-' + m + '-' + d

  }
}



