<template lang="jade">
  div#balcklist
    h2#title 
    template
      el-table(v-if="users", :data='users', border='', style='width: 95%')
        el-table-column(v-for="(val, key, index) in base", :prop="key", :label="val", :key="key")
        el-table-column(label='')
          template(scope='scope')
            el-button(size='small', type='success', @click='handleDelete(scope.$index)') 
</template>
<script>
  import api from '../../common/api-admin'
  export default {
    name: 'reserve',
    data () {
      return {
        base: {
          name: '',
          number: '',
          school: '',
          sex: '',
          mail: ''
        },
        msg: '',
        users: ''
      }
    },
    beforeCreate() {
      api.users(1).then((res) => {
        this.users = res
      })
    },
    methods: {
      handleDelete(index) {
        let user = this.users[index];
        this.$confirm(':"' + user.name + '"', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          api.removeFromBlackList(user.number).then((res) => {
            this.users.splice(index, 1);
            this.$message({type: 'success', message: res.msg})
          })
        }).catch(() => {})
      }
    }
  }
</script>
<style scoped lang="sass">
  #balcklist
    flex: 1
    margin-left: 12rem
    padding-left: 1rem
    background-color: #EFF2F7
    padding-bottom: 2rem
    #title
      margin: 20px
      font-size: 2rem
</style>



